<template>
  <div class="ofdbox-viewer-body">
    <Header :task="task" />
    <div v-for="url in images" :key="url" class="page">
      <el-image :key="url" :src="url" lazy></el-image>
    </div>

    <div v-if="task && task.error" class="error-msg">解析错误</div>
  </div>
</template>

<script>
import axios from 'axios'
import Header from './header'
export default {
  components: {
    Header
  },
  data () {
    return {
      id: null,
      task: null,
      interval: -1
    }
  },
  mounted () {
    this.id = this.$route.query.id
    if (!this.id) {
      return
    }
    this.interval = setInterval(this.queryTask, 2000)
  },
  computed: {
    baseUrl () {
      return process.env.VUE_APP_BASE_URL;
    },
    images () {
      if (!this.task) return []
      let urls = []
      this.task.pages.forEach(page => {
        urls.push(this.baseUrl + "/image/" + this.id + "/" + page)
      })
      return urls
    }
  },
  methods: {
    queryTask () {
      axios.get(this.baseUrl + "/task/" + this.id).then(res => {
        if (res.status === 200) {
          res = res.data
          if (res.code === 200) {
            this.task = res.data
            if (this.task.error || this.task.complete) {
              clearInterval(this.interval)
            }
          }
        }
      }).catch(e => {
        if ("Request failed with status code 404" === e.message) {
          console.log(e.message)
          clearInterval(this.interval)
        }
      })
    }
  }
}
</script>

<style scoped>
.ofdbox-viewer-body {
  background-color: rgb(82, 86, 89);
  padding-top: 70px;
  min-height: calc(100vh - 70px);
}
.page {
  padding-left: 10%;
  padding-right: 10%;
  margin-top: 10px;
}

.error-msg {
  font-size: 30px;
  color: aliceblue;
  text-align: center;
}
</style>